export default [
  { path: '/eltable',
    // redirect: '/question/question',
    name: 'eltable',
    component: resolve => require(['@/view/el-table/index'], resolve),
    children: [
      {path: 'demo', name: 'demo', component: resolve => require(['@/view/el-table/demo'], resolve)},
      {path: 'example', name: 'example', component: resolve => require(['@/view/el-table/example'], resolve)},
      {path: 'data', name: 'data', component: resolve => require(['@/view/el-table/data'], resolve)},
      {path: 'svgIcon', name: 'svgIcon', component: resolve => require(['@/view/el-table/svgIcon'], resolve)},
      {path: 'canvas', name: 'canvas', component: resolve => require(['@/view/el-table/canvas'], resolve)},
      {path: 'tinymetest', name: 'tinymetest', component: resolve => require(['@/view/el-table/tinymetest'], resolve)},
      {path: 'upload', name: 'upload', component: resolve => require(['@/view/el-table/upload'], resolve)},
      {path: 'slot', name: 'slot', component: resolve => require(['@/view/el-table/slot'], resolve)},
      {path: 'time', name: 'time', component: resolve => require(['@/view/el-table/time'], resolve)}
    ]
  }
]



