export default [
  { path: '/map',
    // redirect: '/question/question',
    name: 'map',
    component: resolve => require(['@/view/map/index'], resolve),
    children: [
      {path: 'mainmap', name: 'mainmap', component: resolve => require(['@/view/map/mainmap'], resolve)},
      {path: 'mapmenu', name: 'mapmenu', component: resolve => require(['@/view/map/mapmenu'], resolve)},
      {path: 'mxMap', name: 'mxMap', component: resolve => require(['@/view/map/mxMap'], resolve)}
    ]
  }
]



