export default [
  { path: '/safe',
    // redirect: '/question/question',
    name: 'safe',
    component: resolve => require(['@/view/safe/index'], resolve),
    children: [
      {path: 'CSRF', name: 'CSRF', component: resolve => require(['@/view/safe/CSRF'], resolve)},
      {path: 'XSS', name: 'XSS', component: resolve => require(['@/view/safe/XSS'], resolve)},
      {path: 'SQL', name: 'SQL', component: resolve => require(['@/view/safe/SQL'], resolve)},
      {path: 'list', name: 'list', component: resolve => require(['@/view/safe/list'], resolve)},
      {path: 'Blog', name: 'Blog', component: resolve => require(['@/view/vueaxios/Blog'], resolve)}
    ]
  }
]



