export default [
  { path: '/javaScript',
    // redirect: '/question/question',
    name: 'javaScript',
    component: resolve => require(['@/view/javaScript/index'], resolve),
    children: [
      {path: 'variate', name: 'variate', component: resolve => require(['@/view/javaScript/variate'], resolve)},
      {path: 'Regex', name: 'Regex', component: resolve => require(['@/view/javaScript/Regex'], resolve)},
      {path: 'DOM', name: 'DOM', component: resolve => require(['@/view/javaScript/DOM'], resolve)},
      {path: 'operator', name: 'operator', component: resolve => require(['@/view/javaScript/operator'], resolve)}
    ]
  }
]



