export default [
  { path: '/question',
    // redirect: '/question/question',
    name: 'question',
    component: resolve => require(['@/view/vueelement/question'], resolve),
    children: [
      {path: 'question0', name: 'question0', component: resolve => require(['@/view/vueelement/question0'], resolve)},
      {path: 'question1', name: 'question1', component: resolve => require(['@/view/vueelement/question1'], resolve)},
      {path: 'question2', name: 'question2', component: resolve => require(['@/view/vueelement/question2'], resolve)}
    ]
  }
]



